import React from 'react';
import UA_logo from "../Assets/Logos/UA_Logo.png";
import Fb_icon from "../Assets/RS_FB.png";
import Insta_icon from "../Assets/RS_INSTA.png";
import Mail_icon from "../Assets/RS_MAIL.png";
import './Footer.css';

export default class Footer extends React.Component{
    constructor(props) {
        super(props);
    }

    render() {
        return(
            <footer id={"Footer-container"}>
                <div id={"Footer-UAAsso"}>
                    <img id={"Footer-UALogo"} alt={"Logo de l'UA"} src={UA_logo}/>
                    <h2 className={"Footer-h2"}> Union des Associations, association de loi 1901</h2>
                </div>
                <div id={"Footer-Contacts"}>
                    <a href={"https://www.facebook.com/ua.centrale.marseille"}><img src={Fb_icon} className={"Footer-RSlogos"} alt={"LogoRS"}/></a>
                    <a href={"https://www.instagram.com/union_des_associations_ecm/"}><img src={Insta_icon} className={"Footer-RSlogos"} alt={"LogoRS"}/></a>
                    <a id={"FooterMail-a"} href={"mailto:uaecm@ec-m.fr"}><img src={Mail_icon} className={"Footer-RSlogos"} alt={"LogoRS"}/><h2 className={"Footer-h2"}>uaecm@ec-m.fr </h2></a>
                </div>
            </footer>
        )
    }
}