export default class SplashPage extends React.Component{
    constructor(props){
        super(props);
    }

    render() {
        return(
            <div className={"Pages"} id="SplashPage-Container">
                <img src={UA_logo} width={300}/>
                <h1 id={"SplashPage-h1"}> Chargement... </h1>
            </div>
        )
    }
}