import React, {lazy, Suspense} from 'react';
import {
    Switch,
    Route,
    Link,
    withRouter
} from "react-router-dom";
import UA_logo from "../Assets/Logos/UA_Logo.png";
import SplashPage from "../Pages/SplashPage";
import ScrollToTop from "./ScrollToTop";

const Accueil = lazy(() => import("../Pages/Accueil"));
const VieAssociative = lazy(() => import("../Pages/VieAssociative"));
const ManifestationAssociative = lazy(() => import("../Pages/ManifestationAssociative"));

class NavigationRouter extends React.Component{
    constructor(props) {
        super(props);
        this.Pages = {
            Accueil: ["", <Accueil/>, "Accueil"],
            VieAssociative: ["VieAssociative", <VieAssociative/>, "Vie associative"],
          //  ManifestationAssociative: ["ManifestationAssociative", <ManifestationAssociative/>, "Manifestation associative"],
        };
        this.ListNavLi = [
            <li className={"liMenuNavigation"} id={"liMenuNavigationIDlogo"} style={{opacity: 0}}>
                <Link to={"/"}><img src={UA_logo} id={"uaLogoMenu"} alt={"Logo Union des Associations de l'École centrale de Marseille"}/></Link>
            </li>];
        this.ListSwitch = [];
        let compt = 0;
        for (let Page in this.Pages) {
            this.ListNavLi.push(
                <li className={"liMenuNavigation"} id={"liMenuNavigationID" + this.Pages[Page][0]}>
                    <Link to={"/" + this.Pages[Page][0]}>{this.Pages[Page][2]}</Link>
                </li>
            );

            if (compt===0){
                this.ListSwitch.push(
                    <Route exact path={"/" + this.Pages[Page][0]} component={() => this.Pages[Page][1]}/>
                )
            }

            else{
                this.ListSwitch.push(
                    <Route path={"/" + this.Pages[Page][0]} component={() => this.Pages[Page][1]}/>
                )
            }
            compt++;
        }

        if (window.innerWidth > 768){
            this.Router =
                <Suspense fallback={<SplashPage/>}>
                    <ScrollToTop/>
                    <div>
                            <nav id={"MenuNavigation"}>
                                <ul id={"ulMenuNavigation"}>
                                    {this.ListNavLi}
                                </ul>
                            </nav>

                            <Switch>
                                {this.ListSwitch}
                            </Switch>
                        </div>
                </Suspense>
        }
        else{
            this.ListNavLi.splice(0,1)
            this.Router =
                <Suspense fallback={<SplashPage/>}>
                    <ScrollToTop/>
                    <div>
                            <div id={"MobileHorizontalBarMenu"}>
                                <Link to="/"><img src={UA_logo} id={"uaLogoMenu"} alt={"Logo Union des Associations de l'École centrale de Marseille"}/></Link>
                                <div id={"BurgerButtonMenu"} onClick={() => DeroulementMenuMobile()}>
                                    <div className={"BarBurgerButtonMenu"}/>
                                    <div className={"BarBurgerButtonMenu"}/>
                                    <div className={"BarBurgerButtonMenu"}/>
                                </div>
                            </div>

                            <nav style={{opacity: 0}} id={"MenuNavigation"}>
                                <ul id={"ulMenuNavigation"}>
                                    {this.ListNavLi}
                                </ul>
                            </nav>

                            <Switch>
                                {this.ListSwitch}
                            </Switch>
                        </div>
                </Suspense>
        }
    }

    render() {
        return(
            <header id={"Router-container"}>
                {this.Router}
            </header>
        )
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        window.onscroll = function () {ApparitionMenu()}
    }
}

function ApparitionMenu(){
    if (window.innerWidth>768) {
        if (window.scrollY>30){
            let LogoUAMenuBar = document.getElementById("liMenuNavigationIDlogo")
            LogoUAMenuBar.style.opacity = 1
            let ulMenuNavigation = document.getElementById("ulMenuNavigation")
            ulMenuNavigation.style.backgroundColor = "rgba(19, 67, 91, 0.7)"
        }
        else {
            let LogoUAMenuBar = document.getElementById("liMenuNavigationIDlogo")
            LogoUAMenuBar.style.opacity = 0
            let ulMenuNavigation = document.getElementById("ulMenuNavigation")
            ulMenuNavigation.style.backgroundColor = "rgba(19, 67, 91, 0)"
        }
    }
    else {
        if (window.scrollY>30){
            let MobileHorizontalBarMenu = document.getElementById("MobileHorizontalBarMenu")
            MobileHorizontalBarMenu.style.backgroundColor = "rgba(19, 67, 91, 0.7)"
            let uaLogoMenu = document.getElementById("uaLogoMenu")
            uaLogoMenu.style.opacity = 1
        }
        else{
            let MobileHorizontalBarMenu = document.getElementById("MobileHorizontalBarMenu")
            MobileHorizontalBarMenu.style.backgroundColor = "rgba(19, 67, 91, 0)"
            let uaLogoMenu = document.getElementById("uaLogoMenu")
            uaLogoMenu.style.opacity = 0
        }
    }
}

function DeroulementMenuMobile(){
    let MenuNavigation = document.getElementById("MenuNavigation")
    if (MenuNavigation.style.opacity === "0"){
        MenuNavigation.style.opacity = "1"
        let MobileHorizontalBarMenu = document.getElementById("MobileHorizontalBarMenu")
        MobileHorizontalBarMenu.style.backgroundColor = "rgba(19, 67, 91, 0.7)"
        let uaLogoMenu = document.getElementById("uaLogoMenu")
        uaLogoMenu.style.opacity = 1
    }
    else{
        MenuNavigation.style.opacity = "0"
    }
}

export default withRouter(NavigationRouter);